import {Line,mixins } from 'vue-chartjs'

export default Line.extend({
  props: ['data', 'options'],
  mounted () {

    this.renderChart(this.data, this.options)
  },

  watch: {
    options () {
      this.renderChart(this.data, this.options);
    },
    data () {
      console.log("数据的变化：",this.data);
      this.renderChart(this.data, this.options);
    }
  }
})



